* {
    box-sizing: border-box;
  }
  
  *::before,
  *::after {
    box-sizing: border-box;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
    padding: 0;
  }
  
  p {
    margin: 0;
    font-weight: 500;
  }
  
  body {
    font-weight: 700;
    font-family: "Inter";
  }
  
  img {
    max-width: 100%;
  }
  
  ul,
  li {
    margin: 0;
    padding: 0;
    list-style-type: none;
  }
  
  button {
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
    color: #fff;
  }
  
  a {
    text-decoration: none;
    color: #fff;
  }
  :root {
    --darkBg:#17143c;
    --titleGradient: linear-gradient(
        90deg,
        #fff 0%,
        #fabeff 10% 77%,
        #f8adff 85.71%,
        #ffc2af 100%
      );
  }
  
  .unselectable-word {
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
    -khtml-user-select: none; /* Konqueror */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently                                   not supported by any browser */
  }
  
  .be-relative {
    position: relative;
  }
  
  .container {
    max-width: 900px;
    padding: 0 15px;
    margin: 0 auto;
  }
  
  .stn-title {
    font-size: 40px;
    line-height: 48px;
    opacity: 0.9;
    background-color: #fff;
    background-image: var(--titleGradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
  }
  
  .gradient {
    background-color: #fff;
    background-image: var(--titleGradient);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    text-shadow: 0px 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
  }
  
  .hidden {
    overflow: hidden;
  }

  /* ВСТАВКИ ИЗ ПЕРВОГО ПРОЕКТА */
  @media all and (min-width: 1025px) {
    .header.presale .header__body {
      justify-content: center;
      position: relative;
    }
    .header.presale .header__button {
      position: absolute;
      right: 0;
    }
  }
  @media all and (max-width: 1024px) {
    .header.presale .header__body {
      flex-direction: column;
      align-items: center;
    }
    .header.presale .header__logo {
      margin-bottom: 40px;
    }
    .header.presale .header__button {
      display: block;
    }
  }
  .header .header__body {
    display: flex;
    max-width: 1350px;
    margin: 0 auto;
    padding-top: 20px;
  }
  .header .header__logo {
    display: flex;
    align-items: center;
  }
  .header .header__logo .image {
    width: 46px;
    height: auto;
  }
  .header .header__logo-image {
    position: relative;
  }
  .header .header__logo-image img {
    min-width: 194px;
    max-width: 194px;
    height: auto;
  }
  .header .header__logo-image::before {
    content: '';
    width: 80px;
    height: 80px;
    position: absolute;
    top: -20px;
    left: -20px;
    background: radial-gradient(46.06% 46.06% at 50% 50%, #3D2796 0%, rgba(63, 40, 154, 0) 100%);
    opacity: 0.39;
  }
  .header .header__nav-items {
    display: flex;
  }
  .header .header__nav-item {
    margin: 0 15px;
  }
  .header .header__nav-item a {
    color: #fff;
    text-decoration: none;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 19px;
    opacity: 0.9;
  }
  .header .header__languages {
    position: relative;
  }
  .header .header__languages-icon {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #fff;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
  }
  .header .header__languages-icon svg {
    width: 10px;
    height: auto;
    margin-left: 10px;
  }
  .header .header__languages-icon img {
    height: 12px;
    width: auto;
    display: block;
    margin-left: 10px;
  }
  .header .header__languages-list {
    position: absolute;
    top: 100%;
    right: 0;
    border-radius: 5px;
    box-sizing: border-box;
    opacity: 0;
    pointer-events: none;
    transition: opacity 0.2s ease-in-out;
    padding-top: 12px;
  }
  .header .header__languages-list.active {
    opacity: 1;
    pointer-events: visible;
    transition: opacity 0.2s ease-in-out;
  }
  .header .header__languages-list-item {
    text-align: right;
    margin-bottom: 12px;
    box-sizing: border-box;
    font-size: 16px;
    color: #fff;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: 400;
    cursor: pointer;
    user-select: none;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .header .header__languages-list-item span {
    transition: color 0.2s ease-in-out;
  }
  .header .header__languages-list-item img {
    height: 12px;
    width: auto;
    display: block;
    margin-left: 5px;
  }
  .header .header__languages-list-item:hover span {
    color: #FABEFF;
    transition: color 0.2s ease-in-out;
  }
  .header .header__button {
    position: relative;
  }
  .header .header__button a {
    border: none;
    font-family: 'Inter', sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 22px;
    opacity: 0.9;
    text-shadow: 0px 10.9012px 25.4362px rgba(23, 13, 36, 0.1);
    color: #fff;
    background: linear-gradient(135deg, #40DDFF 0%, #14BAE3 19.24%, #13B1E6 68.64%, #11AADF 81.77%, #0B98C5 100%);
    border-radius: 9.10004px;
    padding: 16px 25px;
    box-sizing: border-box;
    cursor: pointer;
    text-decoration: none;
    white-space: nowrap;
  }
  .header .header__button::before {
    content: '';
    position: absolute;
    top: -7px;
    left: -53px;
    width: 304px;
    height: 79px;
    opacity: 0;
    transition: opacity 0.4s ease-in-out;
    background: linear-gradient(90.3deg, #22C7FF 2.07%, #ED1EFF 95.15%);
    filter: blur(66px);
  }
  .header .header__button:hover::before {
    opacity: 0.3;
    transition: opacity 0.4s ease-in-out;
  }
  @media all and (min-width: 1025px) {
    .header .header__body {
      justify-content: space-between;
      align-items: center;
      padding-bottom: 80px;
    }
  }
  @media all and (max-width: 1024px) {
    .header .header__body {
      justify-content: space-between;
      align-items: center;
      margin-bottom: 40px;
    }
    .header .header__nav {
      display: none;
    }
    .header .header__button {
      display: none;
    }
  }
  /* ВСТАВКИ ИЗ ПЕРВОГО ПРОЕКТА */

  .hero {
      background: var(--darkBg);
      padding-bottom: 120px;
  }

  .hero__container {
      max-width: 1170px;
  }

  .hero__wrapper {
      position: relative;
      display: flex;
      gap: 50px;
      justify-content: center;
      flex-wrap: wrap;
      align-items: center;
      z-index: 1;
  }

  .hero__wrapper::after {
      content: '';
      position: absolute;
      left: 30%;
      top: 0;
      width: 634px;
      height: 634px;
      background: #E5A7D0;
      filter: blur(1200px);
      border-radius: 50%;
      z-index: -1;
  }

  .hero__content {
      max-width: 593px;
  }

  .hero__subtitle {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    color: #FFFFFF;  
    margin-bottom: 40px;  
  }

  .hero__title {
    font-size: 50px;
    line-height: 61px;
    letter-spacing: -1.77447px;
    margin-bottom: 56px;
    font-weight: 700;
  }

  .hero__descr {
    font-weight: 500;
    font-size: 16px;
    line-height: 144.02%;
    color: #FFFFFF;
    margin-bottom: 87px;
  }

  .hero__btn {
    position: relative;
    font-size: 26.7205px;
    line-height: 32px;
    font-weight: 700;
    padding: 30px 64px;
    background: linear-gradient(91.81deg, #40DDFF 1.26%, #9505D9 99.17%);
    border-radius: 12.1579px;
    z-index: 1;
  }

  .hero__btn::after {
      content: "";
      position: absolute;
      width: 450px;
      height: 180px;
      background: linear-gradient(90.3deg, #22C7FF 2.07%, #ED1EFF 95.15%);
      opacity: 0.7;
      filter: blur(101px);
      border-radius: 50%;
      top: 0;
      left: -30%;
      z-index: -1;
  }

  .hero__img-mobile {
      display: none;
  }

  .features {
      background: #fff;
      padding-top: 100px;
      padding-bottom: 100px;
  }

  .features__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 58px;
    text-align: center;
    letter-spacing: -1.77447px;
    color: #5C4AA3;
    mix-blend-mode: normal;
    opacity: 0.9;
    margin-bottom: 66px;
  }

  .features__caption {
    font-size: 18px;
    line-height: 120.52%;
    color: rgba(69, 59, 199, 1);
    margin-bottom: 18px;
  }

  .features__text {
    font-size: 16px;
    line-height: 153.52%;
    color: #6F5C8C;
    font-weight: 400;
  }

  .features__wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 70px;
  }

  .advantages {
    background: var(--darkBg);
    padding-top: 100px;
    padding-bottom: 100px;
  }

  .advantages__container {
  }

  .advantages__item {
      position: relative;
      gap: 60px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      max-width: 900px;
      z-index: 1;
  }

  .advantages__item:not(:last-child) {
      margin-bottom: 150px;
  }


  .advantages__content {
  }

  .advantages__title {
    text-align: left;
    font-weight: 700;
    font-size: 40px;
    line-height: 48px;
    letter-spacing: -1.77447px;
    margin-bottom: 23px;
  }

  .advantages__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 153.52%;
    color: #AB97C8;
    mix-blend-mode: normal;
    opacity: 0.9;
  }

  .advantages__item::after {
      position: absolute;
      left: 0;
      top: 0;
      width: 100px;
      height: 100px;
      background: radial-gradient(50% 50% at 50% 50%, #382FA3 0%, rgba(113, 102, 255, 0) 100%);
      filter: blur(22px);
      border-radius: 30px;
      content: '';
      z-index: -1;
  }

  .multi {
      padding-top: 100px;
      /* padding-bottom: 100px; */
  }

  .multi__wrapper {
    display: flex;
    gap: 50px;
  }

  .multi__title {
    font-size: 48px;
    line-height: 58px;
    text-align: center;
    letter-spacing: -1.77447px;
    color: #5C4AA3;
    margin-bottom: 23px;
  }

  .multi__descr {
    font-weight: bold;
    font-size: 18px;
    line-height: 120.52%;
    /* or 22px */
    color: #5C4AA3;
    text-align: center;
    margin-bottom: 120px;
  }

  .multi__content {
      margin-bottom: 100px;
  }

  .multi__caption {
    font-weight: bold;
    font-size: 18px;
    color: #5C4AA3;
    line-height: 120.52%;
    margin-bottom: 17px;
  }

  .multi__text {
    font-weight: 400;
    font-size: 17px;
    line-height: 153.52%;
    color: #6F5C8C;
  }

  .multi__img { 
      max-height: 612px;
      max-width: 587px;
      position: relative;
  }

  .multi__img::after {
    position: absolute;
    width: 100%;
    height: 100%;
    content: "";
    background: red;
  }

  .download {
    background: #17143C;
    padding-top: 100px;
    position: relative;
    z-index: 1;
  }

  .download::after {
    content: '';
    position: absolute;
    left: 30%;
    top: 0;
    width: 634px;
    height: 634px;
    background: #E5A7D0;
    filter: blur(1200px);
    border-radius: 50%;
    z-index: -1;
}

  .download__title {
    font-weight: bold;
    font-size: 34px;
    line-height: 41px;
    text-align: center;
    letter-spacing: -1.77447px;
    margin-bottom: 66px;
  }

  .download__buttons {
      display: flex;
      justify-content: center;
      gap: 15px;
      margin-bottom: 77px;
  }

  .download__img {
      margin-left: 50px;
  }

  .footer {
      background: var(--darkBg);
      position: relative;
  }

  .footer::after {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      background: url("../../public/images/footer_mask.png");
      background-repeat: repeat-x;
      width: 100%;
      height: 154px;
  }

  .footer__container {
      padding-top: 100px;
      text-align: center;
      padding-bottom: 70px;
  }

  .footer__logo {
      margin-bottom: 32px;
  }

  .footer__links {
      display: flex;
      gap: 10px;
      justify-content: center;
  }
  
  @media (max-width: 1400px) {
    .container {
      max-width: 900px;
    }
  }
  
  @media (max-width: 1200px) {

    .hero__wrapper::after {
        width: 50%;
        filter: blur(500px);
    }

    .hero__btn::after {
        width: 100%;
        left: 0;
    }

    .hero__content {
        max-width: unset;
    }

    .hero__img-mobile {
        display: block;
    }

    .hero__img {
        display: none;
    }

    .advantages__title {
        font-size: 30px;
    }

  }
  
  @media (max-width: 992px) {
    .container {
      max-width: 750px;
    }

    .features__wrapper {
        grid-template-columns: 1fr;
    }

    .multi__wrapper {
        flex-wrap: wrap;
    }

    .multi {
        padding-bottom: 100px;
    }

    .multi__content {
        margin-bottom: 30px;
    }

    .download::after {
        width: 100%;
        left: 0;
    }

    .download__img {
        margin-left: 0;
    }
  }
  
  @media (max-width: 768px) {
    .container {
      max-width: 570px;
    }

    .advantages__item {
        flex-wrap: wrap;
        gap: 30px;
    }

    .advantages__item:not(:last-child) {
        margin-bottom: 100px;
    }

    .multi__img {
        max-width: -webkit-fill-available;
    }

  }
  
  @media (max-width: 576px) {
    .container {
      max-width: 100%;
    }

    .hero__title {
        font-size: 38px;
        line-height: 45px;
    }

    .hero__descr {
        margin-bottom: 45px;
    }
  }